* {
  padding: 0;
  margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  width: 100%;
  height: 100vh;
  background: #2c3e50;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App h1{
  background: #27ae60;
  color: white;
  margin-top: .8rem;
}
.app-container{
  background: #ecf0f1;
  width: 400px;
  height: 500px;
  border-radius: 8px;
 box-shadow: 0 0 10px rgba(0,0,0,0.6);
}
.app-container input, .app-container button{
  border: none;
  background: none;
  outline: none;
  margin-top: 1.6rem;
}
.app-container input{
   padding: .4rem 1rem;
  border-bottom: 2px solid #27ae60;
  font-size: 1.1rem;

}
.app-container button{
  color: #27ae60;
  font-size: 2rem;
  border-radius: 50%;
  /* line-height: 2rem; */
  cursor: pointer;
}
.app-container button{
  margin-right: .8rem;
}
.app-container ol{
  /* background-color: yellow; */
  margin: 1rem 2rem;
  text-align: left;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.app-container ol li button{
  color: red;
}
.todo-style{
  /* background-color: yellow; */
  display: flex;
  justify-content: left;
  align-items: center;
  flex-direction: row;
}
.todo-style button{
  margin-bottom:17px ;
  color: red;
}
.app-container ol li{
  list-style: none;
  font-size: 1.4rem;
  
}